.App {
  text-align: center;
}

.App-logo {
  animation: App-logo-spin infinite 20s linear;
  height: 40vmin;
  pointer-events: none;
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

.error{
  margin-left: 5px;
  font-size: 13px;
  color: red;
}

input{
    border: 1px solid #007bff !important;
}

label{
  color: #007bff;
}

.form-control::-webkit-input-placeholder { color: #6699cc; }  /* WebKit, Blink, Edge */
.form-control:-moz-placeholder { color: #6699cc; }  /* Mozilla Firefox 4 to 18 */
.form-control::-moz-placeholder { color: #6699cc; }  /* Mozilla Firefox 19+ */
.form-control:-ms-input-placeholder { color: #6699cc; }  /* Internet Explorer 10-11 */
.form-control::-ms-input-placeholder { color: #6699cc; }  /* Microsoft Edge */
